<template>
  <v-container>
 
    <v-card elevation="1" class="mt-2">
      <v-card-title>
        <v-icon class="mx-2">
          mdi-file-chart
        </v-icon>
        <v-toolbar-title>Report</v-toolbar-title>
      </v-card-title>
      <v-card-text>
        <v-row v-if="!gLoading">
          <v-col cols="12" sm="7"  >
            <app-date-picker v-model="dates"></app-date-picker>
          </v-col>
          <v-col cols="12" sm="5"  >
            <app-auto-complete hideAddNew outlined hide-details rounded dense label="Department" v-model="department" collection="department" itemText="name" itemValue="_id"></app-auto-complete>
          </v-col>
          <v-col cols="12" sm="5"  >
            <v-select label="Report by" outlined hide-details rounded dense :items="byItems" v-model="by"></v-select>
          </v-col>
          <v-col cols="12" sm="7"  >
            <app-auto-complete hideAddNew outlined hide-details rounded dense v-model="autocomplete.value" v-bind="autocomplete.bind"></app-auto-complete>
          </v-col>
          <v-col cols="12" sm="3" >
              <v-btn large rounded color="green white--text" width="100%" @click="submit">Submit</v-btn>
          </v-col>
        </v-row>
        <v-row v-else>
          <v-col cols="12" sm="7"  >
            <v-skeleton-loader
              type="card-heading"
            >
            </v-skeleton-loader>
          </v-col>
          <v-col cols="12" sm="5"  >
             <v-skeleton-loader
              type="card-heading"
            >
            </v-skeleton-loader>
          </v-col>
          <v-col cols="12" sm="5"  >
             <v-skeleton-loader
     
              type="card-heading"
            >
            </v-skeleton-loader>
          </v-col>
          <v-col cols="12" sm="7"  >
             <v-skeleton-loader
              
              type="card-heading"
            >
            </v-skeleton-loader>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <v-card elevation="1" class="my-4">
      <v-data-table 
        class="my-5 elevation-1"
        :loading="loading"
        :headers="headers"
        :single-expand="true"
        :expanded.sync="expanded"
        show-expand
        item-key="_id"
        :items="items">
        <template v-slot:loading   >
            <v-skeleton-loader
            class="mt-6"
            height="94"
            type="list-item-three-line"
          >
          </v-skeleton-loader>
        </template>
        <template v-slot:item.amount="{ item }" >
            {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
        </template> 
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length" class="pa-4">
            <v-data-table :headers="dataHeaders" :items="item.child" @click:row="innerListClick($event, item)">
              <template v-slot:item.amount="{ item }" >
                  {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
              </template>   
              
              <template v-slot:item.created_on="{ item }"  >
                {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
              </template>
              
              
            </v-data-table>
          </td>
        </template> 
      </v-data-table>
    </v-card>

    <v-dialog v-model="dialog.value" max-width="960">
      <v-card>
        <v-card-title>
          {{ dialog.title }}
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog.value = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-card-text>
            <v-data-table :headers="dialog.headers" :items="dialog.items">
              <template v-slot:item.amount="{ item }" >
                  {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
              </template>   
              
              <template v-slot:item.created_on="{ item }"  >
                {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
              </template>
              
              
            </v-data-table>
          </v-card-text>
        </v-card-title>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import utility from './../utility'
import AppAutoComplete from '../components/Autocomplete'
import AppDatePicker from '../components/DateRange'
import { mapActions } from 'vuex'
import dateformat from 'dateformat'

const headers = {
  customer:[
        {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }],
  affiliate: [
    
        {
          text: 'Affiliate',
          align: 'start',
          value: 'affiliate.name',
        },
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }],
  user: [
    
        {
          text: 'Agent',
          align: 'start',
          value: 'user.displayName',
        },
        { text: 'Deposits', value: 'count'},
        { text: 'Total', value: 'amount' }]

}
 
const dataHeaders = {
  customer:[
          {
            text: 'Department',
            value: 'department.name',
        },
        {
          text: 'Date',
          value: 'created_on',
        },
        
        { text: 'Amount', value: 'amount' },
        { text: 'Owner', value: 'user.displayName' }],
  affiliate: [ {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }],
  user: [{
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }]

}
 



const by = [
  { text: 'Customer', value: 'customer' },
  { text: 'Affiliate', value: 'affiliate' },
  { text: 'Agent', value: 'user' }
]

const binds = {
  customer: {
    label: 'Customer',
    collection: 'customer',
    itemText: 'name',
    itemValue: '_id'
  },
  affiliate: {
    label: 'Affiliate',
    collection: 'affiliate',
    itemText: 'name',
    itemValue: '_id'
  },
  user: {
    label: 'Agent',
    collection: 'user',
    itemText: 'displayName',
    itemValue: '_id'
  } 
}

export default {
  name: 'Report',
  components:{
    AppDatePicker,
    AppAutoComplete
  },
  data () {
    return {
      dateformat,
      dates: [...utility.currentDateRange],
      department: '',
      byItems: by,
      by: 'customer',
      autocomplete: {
        value: '',
        bind: binds.customer
      },
      loading: false,
      gLoading: false,
      headers: headers.customer,
      dataHeaders: dataHeaders.customer,
      items: [],
      expanded: [],
      dialog: {
        value: false,
        title: '',
        items: [],
        headers: dataHeaders.customer
      }
      
    }
  },
  watch: {
    by () {
      this.autocomplete.value = ""
      this.autocomplete.bind = binds[this.by]
      this.headers  = headers[this.by]
      this.dataHeaders  = dataHeaders[this.by]
      this.items = []
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async submit () {
      this.loading = true
      this.items = []
      this.expanded = []
      const result = await this.get({
        collection: 'report_new',
        query: {
          department: this.department || null,
          by: this.by,
          created_on: this.dates.join('|'),
          id: this.autocomplete.value || null
        }
      })
      this.items = result.data
      this.loading = false

      console.log(result);
    },
    innerListClick (item) {
      if (this.by != 'customer') {

        this.dialog.value = true
        this.dialog.items = item.child
        this.dialog.title = `(${ item.customer.tpc }) ${item.customer.name}`
      }
   
    }
  },
  async created () {
    this.gLoading = true
    const retention = await this.get({ collection:'department', query: { type: 'rtn' } })
    if (retention && retention.status == 200)
      this.department = retention.data.docs[0]._id 
    this.gLoading = false
    
  }
}
</script>